import { Route, Link, BrowserRouter as Router } from 'react-router-dom'  
import ReactTooltip from 'react-tooltip';
import Header from './Header';
import Intro from './Intro';
import SpiceyLink from './SpiceyLink'
import Caption from './Caption'
import FurtherReading from './FurtherReading'
import {withRouter} from 'react-router'

function getName(path){
  if(path.length<=1)
    return "Home";
  return path.replace("/","")
}

const ChangeTracker = withRouter(({match, location, history}) => {
  console.log(location.pathname, location.state);
  return <Header active={getName(location.pathname)}/>;
})

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}> 
      <div className="App">
        <ChangeTracker/>
        <Route exact path="/" component={()=>(<>
            <header class="masthead" style={{backgroundImage: "url('img/pointing.jpg')"}}>
              <div class="overlay"></div>
              <div class="container">
                <div class="row">
                  <div class="col-lg-8 col-md-10 mx-auto">
                    <div class="post-heading" style={{marginBottom:"3p"}}>
                      <h1>The Lack of Freedom of Speech On The Internet</h1>
                      <h2 class="subheading">Who is responsible?</h2>
                    </div>
                  </div>
                </div>
                
              </div>
            </header> 
            <div class="container" style={{marginTop:"-50px",marginBottom:"50px"}}>
              <div class="row">
                <div class="col-lg-8 col-md-10 mx-auto">
                <Caption>Image courtesy of <em><SpiceyLink noDec noIFrame href="http://juryboxblog.blogspot.com/2010/11/tragedy-hindsight-bias-and-blame-game.html">The Jury Box Blog</SpiceyLink></em></Caption>
                </div>
              </div>
            </div>
          </>
        )} />


        <article>
          <div class="container">
            <div class="row">
              <div class="col-lg-8 col-md-10 mx-auto">
                <Route exact path="/" component={Intro} />
                <Route path="/the-people" component={Intro} />
                <Route path="/the-companies" component={Intro} />
                <Route path="/the-government" component={Intro} />
                <Route path="/further-reading" component={FurtherReading} />
              </div>
            </div>
          </div>
        </article>

        <hr/>

        <footer>
          <div class="container">
            <div class="row">
              <div class="col-lg-8 col-md-10 mx-auto">
                <ul class="list-inline text-center">
                  <li class="list-inline-item">
                    <SpiceyLink noDec href="https://kihtrak.com" width={500} height={500}>
                      <span class="fa-stack fa-lg">
                        <i class="fas fa-circle fa-stack-2x"></i>
                        <i class="fas fa-user fa-stack-1x fa-inverse"></i>
                      </span>
                    </SpiceyLink>
                  </li>
                  <li class="list-inline-item">
                    <a href="https://github.com/KihtrakRaknas/free-speech" data-tip="View the code powering this website" target="_blank" rel="noopener noreferrer">
                      <span class="fa-stack fa-lg">
                        <i class="fas fa-circle fa-stack-2x"></i>
                        <i class="fab fa-github fa-stack-1x fa-inverse"></i>
                      </span>
                    </a>
                  </li>
                </ul>
                <p class="copyright text-muted">Copyright &copy; Karthik Sankar 2020</p>
              </div>
            </div>
          </div>
        </footer>
      </div>
      <ReactTooltip />
    </Router>  
  );
}

export default App;
